import { Modal, useMantineTheme } from '@mantine/core';
import '../../pages/Auth/Auth.css'
  function ProfileModal({modalOpened , setModalOpened}) {
  const theme = useMantineTheme();

  return (
    <Modal
      overlayColor={theme.colorScheme === 'dark' ? theme.colors.dark[9] : theme.colors.gray[2]}
      overlayOpacity={0.55}
      overlayBlur={3}
      size='55%'
      opened = {modalOpened}
      onClose={()=>setModalOpened(false)}
    >
      <form  className="infoFrom authForm">
                <h3>Form</h3>
                <div>
                    <input type="text" placeholder='First Name' className='infoInput' name='firstname' />
                    <input type="text" placeholder='Last Name' className='infoInput' name='lastname' />
                </div>
                <div>
                    <input type="text" name="Worksat" className='infoInput' placeholder='Works At' />
                </div>
                <div>
                    <input type="text" name="LivesIn" className='infoInput' placeholder='LivesIn'  />
                    <input type="text" name="Country" className='infoInput' placeholder='Country' />
                </div>
                <div>
                <input type="text" name="relationshipstatus" className='infoInput' placeholder='Relationship Status' />
                </div>
                <div>
                    Profile Image
                    <input type="file"  name='profileImg' />
                    Cover Image
                    <input type="file"  name='coverImg' />
                </div>

                <button className='button infobutton' type='submit'>
                   Update
                </button>
            </form>
    </Modal>
  );
}

export default ProfileModal;
